// Import Module
import ReactDOM from "react-dom";
import classes from "./ModalMess.module.css";
import { useSelector, useDispatch } from "react-redux";

// Import Component
import {
  MessageFilled,
  UserOutlined,
  SendOutlined,
  FullscreenExitOutlined,
} from "@ant-design/icons";
import { modalMessActions } from "../redux/storeRedux";
import { Row, Col, Avatar } from "antd";
const ModalMess = () => {
  const dispatch = useDispatch();
  const openModal = useSelector((state) => state.modalMess.openModal);

  const showModalHandler = () => {
    dispatch(modalMessActions.openModal());
  };
  const closeModal = () => {
    dispatch(modalMessActions.openModal());
  };

  return (
    <div className={classes.modal}>
      {!openModal && (
        <MessageFilled
          className={classes.iconMess}
          onClick={showModalHandler}
        />
      )}
      {openModal && (
        <div className={classes.openModalChat}>
          <Row
            className={classes.header}
            justify="space-between"
            align={"middle"}
          >
            <Col span={18} className={classes.headerTitle}>
              <p>Customer support</p>
            </Col>
            <Col span={4} className={classes.iconClose}>
              <FullscreenExitOutlined onClick={closeModal} />
            </Col>
          </Row>
          <Row className={classes.contentBox}>
            <Col span={24} className={classes.contentAdmin}>
              <p>Chào mừng bạn đến với BOUTIQUE</p> <br />
              <p>Bạn cần hỗ trợ những gì</p>
            </Col>
            <Col span={24} className={classes.contentUser}>
              <p>Tôi muốn xem những sản phẩm về Iphone</p>
            </Col>
          </Row>
          <Row justify="space-between" className={classes.footer}>
            <Col span={23}>
              <Avatar size="small" icon={<UserOutlined />} />
              <input
                type="text"
                placeholder="Enter Message!"
                className={classes.inputMess}
              />
            </Col>
            <Col span={1} className={classes.icon}>
              <SendOutlined />
            </Col>
          </Row>
        </div>
      )}
    </div>
  );
};

const PopupMess = () => {
  return (
    <>
      {ReactDOM.createPortal(
        <ModalMess />,
        document.getElementById("modal-message")
      )}
    </>
  );
};

export default PopupMess;
