// Import Module
import { useState } from "react";
import classes from "./ImgProducts.module.css";

// Import Component
import { Row, Col } from "antd";

const ImgProducts = ({ productDetail }) => {
  //Create hook
  const [urlImage, setUrlImage] = useState("");

  const changeImageHandler = (img) => {
    console.log("render", productDetail);
    setUrlImage(img);
  };

  return (
    <>
      <img
        className={classes.img}
        src={urlImage || productDetail.img1}
        alt="img-product"
      />
      <Row justify="space-between">
        {productDetail.images.map((item, index) => (
          <Col key={index}>
            <img
              className={classes.anotherImg}
              src={item}
              onClick={() => changeImageHandler(item)}
            />
          </Col>
        ))}
      </Row>
    </>
  );
};

export default ImgProducts;
