// Import Module
import { useEffect, useState } from "react";
import classes from "./BillCart.module.css";

// Import Component
import { Row, Col } from "antd";
import { GiftOutlined } from "@ant-design/icons";

const BillCart = ({ data }) => {
  // Create + Use Hook
  const [total, setTotal] = useState(0);
  useEffect(() => {
    const reduceTotalBillCart = (data) => {
      const resultTotal = data.items.reduce(
        (accumulator, currentValue) => accumulator + currentValue.total,
        0
      );

      setTotal(resultTotal.toString().replace(/\B(?=(\d{3})+(?!\d))/g, "."));
    };

    if (data) {
      reduceTotalBillCart(data);
    }
  }, [data]);

  return (
    <div className={classes.billCart}>
      <div className={classes.container}>
        <h1 className={classes.title}>CART TOTAL</h1>
        <Row justify="space-between" className={classes.subtotal}>
          <Col span={10}>
            <p>SUBTOTAL:</p>
          </Col>
          <Col span={12}>
            <p className={classes.price}>{total} VNĐ</p>
          </Col>
        </Row>
        <Row justify="space-between" className={classes.total}>
          <Col span={10}>
            <p>TOTAL:</p>
          </Col>
          <Col span={14}>
            <p className={classes.price}>{total} VNĐ</p>
          </Col>
        </Row>
        <input
          className={classes.inputCoupon}
          type="text"
          placeholder="Enter your coupon"
        />
        <button className={classes.addCoupon}>
          <GiftOutlined className={classes.iconGift} /> Apply coupon
        </button>
      </div>
    </div>
  );
};

export default BillCart;
