// Import Module
import classes from "./UniqueSellingPoint.module.css";

// Import Component
import { Row, Col, Button, Input, Space } from "antd";

const DUMMY_SELLINGPOINT = [
  { id: 1, title: "FREE SHIPPING", content: "Free shipping worlwide" },
  { id: 2, title: "24 x 7 SERVICE", content: "Free shipping worlwide" },
  { id: 3, title: "FESTIVAL OFFER", content: "Free shipping worlwide" },
];

const UniqueSellingPoint = () => {
  return (
    <>
      <div className={classes.container}>
        <Row className={classes.row} justify="space-around">
          {DUMMY_SELLINGPOINT.map((item) => (
            <Col
              key={item.id}
              className={classes.col}
              span={5}
              lg={8}
              md={8}
              sm={8}
              xs={24}
            >
              <h2>{item.title}</h2>
              <p>{item.content}</p>
            </Col>
          ))}
        </Row>
      </div>
      <div className={classes.section}>
        <Row className={classes.row} justify="space-between">
          <Col className={classes.col} lg={8} md={8} sm={20} xs={20}>
            <h2>LET'S BE FRIENDS!</h2>
            <p>Nisi nisi tempor consequat laboris nisi</p>
          </Col>
          <Col className={classes.col} lg={10} md={12} sm={20} xs={20}>
            <Space.Compact style={{ width: "100%" }}>
              <Input
                className={classes.inputEmail}
                placeholder="Enter your email address"
              />
              <Button type="primary">Subscribe</Button>
            </Space.Compact>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default UniqueSellingPoint;
