// Import Module
import classes from "./Order.module.css";

// Import Component
import { Row, Col } from "antd";

const Order = () => {
  const getUserAPI = JSON.parse(localStorage.getItem("CURRENT_USER"));
  const getDataAPI = JSON.parse(localStorage.getItem("CART_ARRAY"));
  const getUser = getDataAPI.find((item) => item.user === getUserAPI.email);

  const totalOrder = getUser.items
    .reduce((acc, curr) => acc + curr.total, 0)
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ".");

  return (
    <div className={classes.order}>
      <div className={classes.container}>
        <h1 className={classes.title}>YOUR ORDER</h1>
        {getUser.items.map((item, index) => (
          <Row
            key={index}
            justify="space-between"
            className={classes.userOrder}
          >
            <Col span={12}>
              <p className={classes.name}>{item.name}</p>
            </Col>
            <Col span={10} style={{ textAlign: "right" }}>
              <p className={classes.price}>
                {item.total.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}{" "}
                VNĐ
              </p>
              <span>x {item.quantity}</span>
            </Col>
          </Row>
        ))}

        <Row justify="space-between" className={classes.total}>
          <Col span={10}>
            <p>TOTAL:</p>
          </Col>
          <Col span={10}>
            <p className={classes.price}>{totalOrder} VNĐ</p>
          </Col>
        </Row>
      </div>
    </div>
  );
};

export default Order;
