// Import Module
import { useEffect, useState } from "react";
import classes from "./Description.module.css";

// Import Component
import { Button } from "antd";

const Description = ({ productDetail }) => {
  const [descProduct, setDescProduct] = useState([]);

  useEffect(() => {
    if (productDetail.long_desc.includes("•")) {
      setDescProduct(productDetail.long_desc.split("•"));
    } else {
      setDescProduct(productDetail.long_desc.split("-"));
    }
  }, [productDetail]);

  return (
    <div className={classes.container}>
      <Button className={classes.description} type="primary">
        DESCRIPTION
      </Button>
      <h1 className={classes.title}>PRODUCT DESCRIPTION</h1>
      <ul className={classes.ul}>
        {descProduct.map((item, index) => (
          <li key={index} className={classes.titleDesc}>
            {item}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Description;
