// Import Module
import ReactDOM from "react-dom";
import classes from "./Popup.module.css";
import { useDispatch } from "react-redux";
import { popupActions } from "../../redux/storeRedux";
import { useNavigate } from "react-router-dom";

// Import Component
import { Row, Col } from "antd";
import { ShoppingCartOutlined, CloseOutlined } from "@ant-design/icons";

const Backdrop = () => {
  // Create + Use Hook
  const dispatch = useDispatch();

  //Create Event Handler
  const closeModal = (event) => {
    if (!event.target.classList.contains("backdrop")) {
      dispatch(popupActions.showModal());
    }
  };

  return <div className={classes.backdrop} onClick={closeModal}></div>;
};

const Modal = ({ productDetail }) => {
  // Create + Use Hook
  const dispatch = useDispatch();
  const navigate = useNavigate();

  //Create Event Handler
  const closeModalHandler = () => {
    dispatch(popupActions.showModal());
  };

  const transferPageHandler = () => {
    navigate(`/detail/${productDetail._id.$oid}_${productDetail.category}`);
  };

  return (
    <div className={classes.modal}>
      <Row className={classes.row}>
        <Col className={classes.col} span={12}>
          <img src={productDetail.img1} />
        </Col>

        <Col className={classes.infoProduct} span={12}>
          <CloseOutlined
            className={classes.iconClose}
            onClick={closeModalHandler}
          />
          <h1>{productDetail.name}</h1>
          <p className={classes.price}> {productDetail.price} VNĐ</p>
          <p className={classes.description}>{productDetail.short_desc}</p>
          <button onClick={transferPageHandler}>
            <ShoppingCartOutlined
              className={classes.iconCart}
              style={{ fontSize: "18px", marginRight: "5px" }}
            />
            View Detail
          </button>
        </Col>
      </Row>
    </div>
  );
};

const Popup = ({ productDetail }) => {
  return (
    <>
      {productDetail !== null &&
        ReactDOM.createPortal(
          <Backdrop />,
          document.getElementById("overlay-root")
        )}

      {productDetail !== null &&
        ReactDOM.createPortal(
          <Modal productDetail={productDetail} />,
          document.getElementById("modal-root")
        )}
    </>
  );
};

export default Popup;
