// Import Module
import classes from "./ShoppingCart.module.css";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

// Import Component
import { Row, Col } from "antd";
import {
  CaretLeftOutlined,
  CaretRightOutlined,
  DeleteTwoTone,
  ArrowLeftOutlined,
  ArrowRightOutlined,
  SendOutlined,
  ShoppingOutlined,
} from "@ant-design/icons";
import { updateProductActions } from "../../redux/storeRedux";
import { useEffect, useState } from "react";

const ShoppingCart = ({ onGetDataOfCurrentUser }) => {
  // Create + Use Hook
  const { quantityItems, amountDeleteItems } = useSelector(
    (state) => state.update
  );
  const navigate = useNavigate();
  const isLogin = useSelector((state) => state.login.isLogin);

  const dispatch = useDispatch();
  const [API, setAPI] = useState({
    userAPI: false,
    dataAPI: [],
    currentUser: false,
  });

  useEffect(() => {
    // Get API Localstorage
    const getUserAPI =
      JSON.parse(localStorage.getItem("CURRENT_USER")) || false;
    const getDataAPI = JSON.parse(localStorage.getItem("CART_ARRAY")) || [];
    const getUser = getDataAPI.find((item) => item.user === getUserAPI.email);
    onGetDataOfCurrentUser(getUser);
    if (getUserAPI) {
      setAPI((prev) => {
        return { ...prev, userAPI: true };
      });
      if (getUser) {
        setAPI({
          userAPI: getUserAPI,
          dataAPI: getDataAPI,
          currentUser: getUser,
        });
      }
    } else {
      setAPI({
        userAPI: false,
        dataAPI: [],
        currentUser: false,
      });
    }
  }, [quantityItems, isLogin, amountDeleteItems]);

  // // Create Event Handler
  const increaseProductHandler = (itemSelected) => {
    const { dataAPI, currentUser } = API;
    dispatch(
      updateProductActions.increaseProduct({
        itemSelected,
        dataAPI,
        currentUser,
      })
    );
  };

  const decreaseProductHandler = (itemSelected) => {
    const { dataAPI, currentUser } = API;
    dispatch(
      updateProductActions.decreaseProduct({
        itemSelected,
        dataAPI,
        currentUser,
      })
    );
  };

  const deleteProductHandler = (itemSelected) => {
    const { dataAPI, currentUser } = API;
    dispatch(
      updateProductActions.deleteProduct({
        itemSelected,
        dataAPI,
        currentUser,
      })
    );
  };

  const tranferShopPageHandler = () => {
    navigate("/shop");
  };

  const tranferCheckoutPageHandler = () => {
    navigate("/checkout");
  };
  const tranferLoginPageHandler = () => {
    navigate("/login");
  };
  // console.log(API);
  const resultCurrentUser = API.currentUser || API.currentUser.items;

  return (
    <div className={classes.container}>
      <h1 className={classes.title}>SHOPPING CART</h1>
      <Row className={classes.listTitle} justify="space-between">
        <Col className={classes.col} span={3}>
          <p className={classes.titleImg}>IMAGE</p>
        </Col>
        <Col className={classes.col} span={3}>
          <p className={classes.titleProduct}>PRODUCT</p>
        </Col>
        <Col className={classes.col} span={3}>
          <p className={classes.titlePrice}>PRICE</p>
        </Col>
        <Col className={classes.col} span={3}>
          <p className={classes.titleQuantity}>QUANTITY</p>
        </Col>
        <Col className={classes.col} span={3}>
          <p className={classes.titleTotal}>TOTAL</p>
        </Col>
        <Col className={classes.col} span={3}>
          <p className={classes.titleRemove}>REMOVE</p>
        </Col>
      </Row>

      {!API.currentUser && !API.userAPI && (
        <div className={classes.textBoxMessage}>
          <p className={classes.messageLogin}>
            <SendOutlined
              className={classes.iconLogin}
              onClick={tranferLoginPageHandler}
            />
            Đăng nhập để trải nghiệm mua đồ !!
          </p>
        </div>
      )}
      {API.userAPI && resultCurrentUser && (
        <div className={classes.textBoxMessage}>
          <p className={classes.messageLogin}>
            <ShoppingOutlined className={classes.iconLogin} />
            Chưa có sản phẩm nào trong giỏ hàng!!
          </p>
        </div>
      )}

      {API.userAPI &&
        API.currentUser.user &&
        API.currentUser.items.map((item) => (
          <div key={item._id.$oid}>
            <Row className={classes.infoItem} justify="space-between">
              <Col span={3}>
                <img src={item.img1} alt="" />
              </Col>
              <Col span={4}>
                <p className={classes.infoName}>{item.name}</p>
              </Col>
              <Col span={3}>
                <p className={classes.infoPrice}>{item.price}</p>
                <span>VNĐ</span>
              </Col>
              <Col span={3}>
                <div className={classes.amountItem}>
                  <CaretLeftOutlined
                    className={classes.iconDecrease}
                    onClick={() => decreaseProductHandler(item)}
                  />
                  <span className={classes.amount}>{item.quantity}</span>
                  <CaretRightOutlined
                    className={classes.iconIncrease}
                    onClick={() => increaseProductHandler(item)}
                  />
                </div>
              </Col>
              <Col span={3}>
                <p className={classes.infoPrice}>
                  {item.total.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}
                </p>
                <span>VNĐ</span>
              </Col>
              <Col span={3}>
                <DeleteTwoTone
                  className={classes.iconDelete}
                  onClick={() => deleteProductHandler(item)}
                />
              </Col>
            </Row>
          </div>
        ))}

      {API.userAPI && API.currentUser.user && (
        <Row className={classes.transferPage} justify="space-between">
          <Col
            className={classes.col}
            span={8}
            onClick={tranferShopPageHandler}
          >
            <ArrowLeftOutlined className={classes.iconBackPage} />
            <p className={classes.shopping}>Continue Shopping</p>
          </Col>
          <Col className={classes.col} span={8}>
            <button
              className={classes.btnTransferPage}
              onClick={tranferCheckoutPageHandler}
            >
              <p className={classes.checkout}>Proceed to checkout</p>
              <ArrowRightOutlined className={classes.iconNextPage} />
            </button>
          </Col>
        </Row>
      )}
    </div>
  );
};

export default ShoppingCart;
