// Import module
import classes from "./Banner.module.css";

// Import Component
import { Breadcrumb } from "antd";
import { Link } from "react-router-dom";
const Banner = () => {
  return (
    <div className={classes.banner}>
      <h1>CHECKOUT</h1>
      <Breadcrumb
        className={classes.breadcrumb}
        items={[
          {
            title: (
              <Link to="/" className={classes.pageHome}>
                HOME
              </Link>
            ),
          },
          {
            title: (
              <Link to="/cart" className={classes.pageCart}>
                CART
              </Link>
            ),
          },
          {
            title: (
              <Link to="checkout" className={classes.pageCheckout}>
                CHECKOUT
              </Link>
            ),
          },
        ]}
      />
    </div>
  );
};

export default Banner;
