// Import Module
import classes from "./FormLogin.module.css";
import useInputForm from "../../hooks/use-inputForm";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { loginActions } from "../../redux/storeRedux";

// Import Component
import Input from "../RegisterPage/Input";
import { Link, useNavigate } from "react-router-dom";
import { MailOutlined, KeyOutlined } from "@ant-design/icons";
import { Button } from "antd";

const FormLogin = () => {
  // Get user in LocalStorage
  const dataUsers = JSON.parse(window.localStorage.getItem("USER_ARRAY")) || [];

  // Create + use Hook
  const navigate = useNavigate();
  const [showErrorMessage, setShowErrorMessage] = useState({
    showErrorPass: false,
    showErrorEmail: false,
  });
  const dispatch = useDispatch();

  // Input Pass Value
  const {
    enteredValue: enteredPassword,
    valueIsNotEmpty: passwordIsNotEmpty,
    valueInputValid: passwordInputValid,
    inputValueChangeHandler: passwordValueChangeHandler,
    inputValueBlurHandler: inputPasswordBlurHandler,
    resetInput: resetPass,
  } = useInputForm(
    (value) => value.length > 8,
    (value) => value.trim() !== ""
  );

  // Input Email Value
  const {
    enteredValue: enteredEmail,
    valueIsNotEmpty: emailIsNotEmpty,
    valueInputValid: emailInputValid,
    inputValueChangeHandler: emailValueChangeHandler,
    inputValueBlurHandler: inputEmailBlurHandler,
    resetInput: resetEmail,
  } = useInputForm(
    (value) => value.includes("@"),
    (value) => value.trim() !== ""
  );

  // Create Event Handler
  const checkValidate = () => {
    let isCheck = true;
    // Check Form Input
    if (!passwordIsNotEmpty && !emailIsNotEmpty) {
      setShowErrorMessage((prevShow) => {
        return {
          ...prevShow,
          showErrorPass: true,
          showErrorEmail: true,
        };
      });
      isCheck = false;
    }
    return isCheck;
  };

  const signInHandler = (event) => {
    event.preventDefault();
    const isCheck = checkValidate();

    if (isCheck) {
      const userValid = dataUsers.find(
        (item) =>
          item.email === enteredEmail && item.password === enteredPassword
      );
      if (userValid) {
        localStorage.setItem("CURRENT_USER", JSON.stringify(userValid));
        dispatch(loginActions.login());
        navigate("..");
      } else {
        resetPass();
        resetEmail();
        alert("Email hoặc Mật khẩu không hợp lệ !");
      }
    }
  };

  return (
    <div className={classes.container}>
      <h1>Sign In</h1>
      <form className={classes.form} onSubmit={signInHandler}>
        <div className={classes.row}>
          <Input
            inputValueChangeHandler={emailValueChangeHandler}
            inputInputBlurHandler={inputEmailBlurHandler}
            enteredValue={enteredEmail}
            valueInputValid={emailInputValid}
            showErrorMessage={showErrorMessage.showErrorEmail}
            placeholder="Email"
            errorMessage="Email không hợp lệ. (Vd: abc@gmail.com)"
          />
          <MailOutlined className={classes.icon} />
        </div>
        <div className={classes.row}>
          <Input
            inputValueChangeHandler={passwordValueChangeHandler}
            inputInputBlurHandler={inputPasswordBlurHandler}
            enteredValue={enteredPassword}
            valueInputValid={passwordInputValid}
            showErrorMessage={showErrorMessage.showErrorPass}
            placeholder="Password"
            errorMessage="Password phải hơn 8 ký tự"
            type="password"
          />
          <KeyOutlined className={classes.icon} />
        </div>
        <Button className={classes.btn} htmlType="submit" type="primary">
          SIGN IN
        </Button>
        <p className={classes.login}>
          Create an account?
          <Link className={classes.a} to="/register">
            Sign up
          </Link>
        </p>
      </form>
    </div>
  );
};

export default FormLogin;
