// Import module
import { BrowserRouter, Route, Routes } from "react-router-dom";
import { Provider as ProviderRedux, useSelector } from "react-redux";
import store from "./redux/storeRedux";
// Import Component
import HomePage from "./pages/Home";
import ShopPage from "./pages/Shop";
import DetailPage from "./pages/Detail";
import CheckoutPage from "./pages/Checkout";
import CartPage from "./pages/Cart";
import LoginPage from "./pages/Login";
import RegisterPage from "./pages/Register";
import Layout from "./RootLayout/Layout";
import ProviderContext from "./store-context/ProductsContext";
import Category from "./components/ShopPage/Category";
import { useState } from "react";

function App() {
  const [logout, setLogout] = useState();
  const getStateAccountHandler = (state) => {
    setLogout(state);
  };

  return (
    <BrowserRouter>
      <ProviderContext>
        <ProviderRedux store={store}>
          <Routes>
            <Route
              path="/"
              element={<Layout onChangeState={getStateAccountHandler} />}
            >
              <Route index element={<HomePage />} />
              <Route path="/shop" element={<ShopPage />}>
                <Route path=":category" element={<Category />} />
              </Route>
              <Route
                path="/detail/:productID"
                element={<DetailPage logout={logout} />}
              />
              <Route path="/cart" element={<CartPage />} />
              <Route path="/checkout" element={<CheckoutPage />} />
            </Route>
            <Route path="/login" element={<LoginPage />} />
            <Route path="/register" element={<RegisterPage />} />
          </Routes>
        </ProviderRedux>
      </ProviderContext>
    </BrowserRouter>
  );
}

export default App;
