// Import Component
import Banner from "../components/CheckOutPage/Banner";
import { Row, Col } from "antd";
import FormBill from "../components/CheckOutPage/FormBill";
import Order from "../components/CheckOutPage/Order";

const CheckoutPage = () => {
  return (
    <>
      <Banner />
      <Row justify="space-around">
        <Col span={10}>
          <FormBill />
        </Col>
        <Col span={10}>
          <Order />
        </Col>
      </Row>
    </>
  );
};

export default CheckoutPage;
