// Import Module
import classes from "./FormBill.module.css";
import useInputForm from "../../hooks/use-inputForm";
import { useState } from "react";
// Import Component

const FormBill = () => {
  // Create + use Hook
  const [showErrorMessage, setShowErrorMessage] = useState({
    showErrorMessageFullName: false,
    showErrorMessageEmail: false,
    showErrorMessagePhone: false,
    showErrorMessageAddress: false,
  });

  const {
    enteredValue: enteredFullName,
    valueIsNotEmpty: fullNameIsNotEmpty,
    valueInputValid: fullNameInputValid,
    inputValueChangeHandler: fullNameValueChangeHandler,
    inputValueBlurHandler: inputFullNameBlurHandler,
    resetInput: resetFullName,
  } = useInputForm(
    (value) => value.length >= 1,
    (value) => value.trim() !== ""
  );

  const {
    enteredValue: enteredEmail,
    valueIsNotEmpty: emailIsNotEmpty,
    valueInputValid: emailInputValid,
    inputValueChangeHandler: emailValueChangeHandler,
    inputValueBlurHandler: inputEmailBlurHandler,
    resetInput: resetEmail,
  } = useInputForm(
    (value) => value.includes("@gmail.com"),
    (value) => value.trim() !== ""
  );

  const {
    enteredValue: enteredPhone,
    valueIsNotEmpty: phoneIsNotEmpty,
    valueInputValid: phoneInputValid,
    inputValueChangeHandler: phoneValueChangeHandler,
    inputValueBlurHandler: inputPhoneBlurHandler,
    resetInput: resetPhone,
  } = useInputForm(
    (value) => value.length == 10,
    (value) => value.trim() !== ""
  );

  const {
    enteredValue: enteredAddress,
    valueIsNotEmpty: addressIsNotEmpty,
    valueInputValid: addressInputValid,
    inputValueChangeHandler: addressValueChangeHandler,
    inputValueBlurHandler: inputAddressBlurHandler,
    resetInput: resetAddress,
  } = useInputForm(
    (value) => value.length >= 1,
    (value) => value.trim() !== ""
  );
  // Create Event Handler
  const submitHandler = (event) => {
    event.preventDefault();
    if (!fullNameIsNotEmpty) {
      setShowErrorMessage((prev) => {
        return { ...prev, showErrorMessageFullName: true };
      });
    }
    if (!emailIsNotEmpty) {
      setShowErrorMessage((prev) => {
        return { ...prev, showErrorMessageEmail: true };
      });
    }
    if (!phoneIsNotEmpty) {
      setShowErrorMessage((prev) => {
        return { ...prev, showErrorMessagePhone: true };
      });
    }
    if (!addressIsNotEmpty) {
      setShowErrorMessage((prev) => {
        return { ...prev, showErrorMessageAddress: true };
      });
    }
  };

  return (
    <div className={classes.formBill}>
      <h1 className={classes.title}>BILLING DETAILS</h1>
      <form className={classes.form} onSubmit={submitHandler}>
        <div className={classes.row}>
          <label>FULL NAME:</label>
          <input
            className={
              showErrorMessage.showErrorMessageFullName && !fullNameInputValid
                ? classes.inputInvalid
                : classes.input
            }
            type="text"
            placeholder="Enter Your Full Name Here !"
            value={enteredFullName}
            onChange={fullNameValueChangeHandler}
            onBlur={inputFullNameBlurHandler}
          />
          {showErrorMessage.showErrorMessageFullName && !fullNameInputValid && (
            <p className={classes.errorMessage}>
              (*) FullName không được bỏ trống
            </p>
          )}
        </div>
        <div className={classes.row}>
          <label>EMAIL:</label>
          <input
            className={
              showErrorMessage.showErrorMessageEmail && !emailInputValid
                ? classes.inputInvalid
                : classes.input
            }
            type="text"
            placeholder="Enter Your Email Here !"
            value={enteredEmail}
            onChange={emailValueChangeHandler}
            onBlur={inputEmailBlurHandler}
          />
          {showErrorMessage.showErrorMessageEmail && !emailInputValid && (
            <p className={classes.errorMessage}>(*) Email không hợp lệ</p>
          )}
        </div>
        <div className={classes.row}>
          <label>PHONE NUMBER:</label>
          <input
            className={
              showErrorMessage.showErrorMessagePhone && !phoneInputValid
                ? classes.inputInvalid
                : classes.input
            }
            type="text"
            placeholder="Enter Your Phone Number Here !"
            value={enteredPhone}
            onChange={phoneValueChangeHandler}
            onBlur={inputPhoneBlurHandler}
          />
          {showErrorMessage.showErrorMessagePhone && !phoneInputValid && (
            <p className={classes.errorMessage}>(*) Phone number phải 10 số</p>
          )}
        </div>
        <div className={classes.row}>
          <label>ADDRESS:</label>
          <input
            className={
              showErrorMessage.showErrorMessageAddress && !addressInputValid
                ? classes.inputInvalid
                : classes.input
            }
            type="text"
            placeholder="Enter Your Address Here !"
            value={enteredAddress}
            onChange={addressValueChangeHandler}
            onBlur={inputAddressBlurHandler}
          />
          {showErrorMessage.showErrorMessageAddress && !addressInputValid && (
            <p className={classes.errorMessage}>
              (*) Address không được bỏ trống
            </p>
          )}
        </div>
        <button className={classes.btnOrder}>Place order</button>
      </form>
    </div>
  );
};

export default FormBill;
