// Import module
import { useContext, useState } from "react";
import { ProductsContext } from "../../store-context/ProductsContext";
import classes from "./Products.module.css";
import { useDispatch, useSelector } from "react-redux";
import { popupActions } from "../../redux/storeRedux";

// Import Component
import { Row, Col } from "antd";
import Popup from "./Popup";

const Products = () => {
  // create Hooks
  const dataContext = useContext(ProductsContext);
  const [productDetail, setProductDetail] = useState(null);

  // create Dispatch Redux
  const isPopup = useSelector((state) => state.popup.isShow);
  const dispatch = useDispatch();

  // create Event Handler
  const showPopupHandler = (item) => {
    dispatch(popupActions.showModal());
    setProductDetail(item);
  };

  return (
    <div className={classes.products}>
      <p>MADE THE HARD WAY</p>
      <h2>TOP TRENDING PRODUCTS</h2>
      <Row className={classes.row} justify="center">
        {dataContext.map((item) => (
          <Col
            key={item._id.$oid}
            className={classes.col}
            lg={5}
            md={8}
            sm={12}
            xs={24}
          >
            <img
              src={item.img1}
              alt={`img${item._id.$oid}`}
              onClick={() => showPopupHandler(item)}
            />
            <p className={classes.name}>{item.name}</p>
            <p className={classes.price}>{item.price} VNĐ</p>
          </Col>
        ))}
      </Row>

      {isPopup && <Popup productDetail={productDetail} />}
    </div>
  );
};

export default Products;
