// Import Module
import classes from "./FormRegister.module.css";
import { useState } from "react";
import useInputForm from "../../hooks/use-inputForm";

// Import Component
import { Link, useNavigate } from "react-router-dom";
import {
  MailOutlined,
  KeyOutlined,
  UserOutlined,
  PhoneOutlined,
} from "@ant-design/icons";
import { Button } from "antd";
import Input from "./Input";

const FormRegister = () => {
  // Get user in LocalStorage
  const dataUsers = JSON.parse(window.localStorage.getItem("USER_ARRAY")) || [];

  // Create + use Hook
  const navigate = useNavigate();
  const [showErrorMessage, setShowErrorMessage] = useState({
    showErrorName: false,
    showErrorEmail: false,
    showErrorPass: false,
    showErrorPhone: false,
  });

  // Input Name Value
  const {
    enteredValue: enteredName,
    valueIsNotEmpty: nameIsNotEmpty,
    valueInputValid: nameInputValid,
    inputValueChangeHandler: nameValueChangeHandler,
    inputValueBlurHandler: inputNameBlurHandler,
  } = useInputForm(
    (value) => value.length > 1 && value.length < 30,
    (value) => value.trim() !== ""
  );
  // Input Email Value
  const {
    enteredValue: enteredEmail,
    valueIsNotEmpty: emailIsNotEmpty,
    valueInputValid: emailInputValid,
    inputValueChangeHandler: emailValueChangeHandler,
    inputValueBlurHandler: inputEmailBlurHandler,
  } = useInputForm(
    (value) => value.includes("@gmail.com"),
    (value) => value.trim() !== ""
  );
  // Input Pass Value
  const {
    enteredValue: enteredPassword,
    valueIsNotEmpty: passwordIsNotEmpty,
    valueInputValid: passwordInputValid,
    inputValueChangeHandler: passwordValueChangeHandler,
    inputValueBlurHandler: inputPasswordBlurHandler,
  } = useInputForm(
    (value) => value.length > 8,
    (value) => value.trim() !== ""
  );
  // Input Phone Value
  const {
    enteredValue: enteredPhone,
    valueIsNotEmpty: phoneIsNotEmpty,
    valueInputValid: phoneInputValid,
    inputValueChangeHandler: phoneValueChangeHandler,
    inputValueBlurHandler: inputPhoneBlurHandler,
  } = useInputForm(
    (value) => value.length === 10,
    (value) => value.trim() !== ""
  );

  // Create Event Handler

  const checkValidate = () => {
    let isCheck = true;
    // Check Account Unique
    dataUsers.forEach((element) => {
      if (element.email === enteredEmail) {
        alert("Tài khoản Email này đã được sử dụng!. Mời nhập Email khác.");
        isCheck = false;
      }
      if (element.phone === enteredPhone) {
        alert(
          "Số điện thoại này đã được sử dụng!. Mời nhập số điện thoại khác."
        );
        isCheck = false;
      }
    });
    // Check Form Input
    if (
      !nameIsNotEmpty &&
      !emailIsNotEmpty &&
      !passwordIsNotEmpty &&
      !phoneIsNotEmpty
    ) {
      setShowErrorMessage((prevShow) => {
        return {
          ...prevShow,
          showErrorName: true,
          showErrorEmail: true,
          showErrorPass: true,
          showErrorPhone: true,
        };
      });
      isCheck = false;
    }

    if (!nameInputValid) {
      setShowErrorMessage((prevShow) => {
        return { ...prevShow, showErrorName: true };
      });
      isCheck = false;
    }
    if (!emailInputValid) {
      setShowErrorMessage((prevShow) => {
        return { ...prevShow, showErrorEmail: true };
      });
      isCheck = false;
    }
    if (!passwordInputValid) {
      setShowErrorMessage((prevShow) => {
        return { ...prevShow, showErrorPass: true };
      });
      isCheck = false;
    }
    if (!phoneInputValid) {
      setShowErrorMessage((prevShow) => {
        return { ...prevShow, showErrorPhone: true };
      });
      isCheck = false;
    }
    return isCheck;
  };

  const signUpHandler = (event) => {
    event.preventDefault();
    const isCheckValidate = checkValidate();

    if (isCheckValidate) {
      const accountUser = {
        name: enteredName,
        email: enteredEmail,
        password: enteredPassword,
        phone: enteredPhone,
      };
      dataUsers.push(accountUser);
      window.localStorage.setItem("USER_ARRAY", JSON.stringify(dataUsers));
      navigate("/login");
    }
  };

  return (
    <div className={classes.container}>
      <h1>Sign Up</h1>
      <form className={classes.form} onSubmit={signUpHandler}>
        <div className={classes.row}>
          <Input
            inputValueChangeHandler={nameValueChangeHandler}
            inputInputBlurHandler={inputNameBlurHandler}
            enteredValue={enteredName}
            valueInputValid={nameInputValid}
            showErrorMessage={showErrorMessage.showErrorName}
            placeholder="FullName"
            errorMessage="FullName phải từ 1 đến 30 ký tự"
          />
          <UserOutlined className={classes.icon} />
        </div>
        <div className={classes.row}>
          <Input
            inputValueChangeHandler={emailValueChangeHandler}
            inputInputBlurHandler={inputEmailBlurHandler}
            enteredValue={enteredEmail}
            valueInputValid={emailInputValid}
            showErrorMessage={showErrorMessage.showErrorEmail}
            placeholder="Email"
            errorMessage="Email không hợp lệ. (Vd: abc@gmail.com)"
          />
          <MailOutlined className={classes.icon} />
        </div>
        <div className={classes.row}>
          <Input
            inputValueChangeHandler={passwordValueChangeHandler}
            inputInputBlurHandler={inputPasswordBlurHandler}
            enteredValue={enteredPassword}
            valueInputValid={passwordInputValid}
            showErrorMessage={showErrorMessage.showErrorPass}
            placeholder="Password"
            errorMessage="Password phải hơn 8 ký tự"
            type="password"
          />
          <KeyOutlined className={classes.icon} />
        </div>
        <div className={classes.row}>
          <Input
            inputValueChangeHandler={phoneValueChangeHandler}
            inputInputBlurHandler={inputPhoneBlurHandler}
            enteredValue={enteredPhone}
            valueInputValid={phoneInputValid}
            showErrorMessage={showErrorMessage.showErrorPhone}
            placeholder="Phone"
            errorMessage="SĐT bắt buộc phải 10 số "
          />
          <PhoneOutlined className={classes.icon} />
        </div>
        <Button className={classes.btn} htmlType="submit" type="primary">
          SIGN UP
        </Button>

        <p className={classes.login}>
          Login ?
          <Link to="/login" className={classes.a}>
            Click
          </Link>
        </p>
      </form>
    </div>
  );
};

export default FormRegister;
