// Import Module

import { useState } from "react";

const useInputForm = (checkValidateInput, checkValueEmpty) => {
  const [enteredValue, setEnteredValue] = useState("");
  const [enteredValueTouched, setEnteredValueTouched] = useState(false);

  const enteredValueIsValid = checkValidateInput(enteredValue);
  const valueIsNotEmpty = checkValueEmpty(enteredValue);
  const valueInputValid = enteredValueIsValid && enteredValueTouched;

  const inputValueChangeHandler = (event) => {
    setEnteredValue(event.target.value);
  };

  const inputValueBlurHandler = () => {
    setEnteredValueTouched(true);
  };

  const resetInput = () => {
    setEnteredValue("");
  };

  return {
    enteredValue,
    enteredValueIsValid,
    valueIsNotEmpty,
    valueInputValid,
    inputValueChangeHandler,
    inputValueBlurHandler,
    resetInput,
  };
};

export default useInputForm;
