// Import Component
import Banner from "../components/CartPage/Banner";
import { Row, Col } from "antd";
import ShoppingCart from "../components/CartPage/ShoppingCart";
import BillCart from "../components/CartPage/BillCart";
import { useState } from "react";

const CartPage = () => {
  const [dataOfCurrentUser, setDataOfCurrentUser] = useState(null);
  const getDataOfCurrentUser = (data) => {
    setDataOfCurrentUser(data);
  };
  return (
    <>
      <Banner />
      <Row justify="space-around" style={{ marginBottom: "80px" }}>
        <Col span={14}>
          <ShoppingCart onGetDataOfCurrentUser={getDataOfCurrentUser} />
        </Col>
        <Col span={8}>
          <BillCart data={dataOfCurrentUser} />
        </Col>
      </Row>
    </>
  );
};

export default CartPage;
