// Import Module
import { useContext } from "react";
import { ProductsContext } from "../../store-context/ProductsContext";
import classes from "./RelatedProduct.module.css";
import { useNavigate } from "react-router-dom";

// Import Component
import { Row, Col } from "antd";

const RelatedProduct = ({ category, id }) => {
  const navigate = useNavigate();
  const data = useContext(ProductsContext);
  const filteredData = data.filter(
    (item) => item.category === category && item._id.$oid !== id
  );

  const changeProductHandler = (item) => {
    navigate(`/detail/${item._id.$oid}_${item.category}`);
    window.scrollTo({
      top: 300,
      left: 0,
    });
  };

  return (
    <div className={classes.container}>
      <h1 className={classes.title}>RELATED PRODUCTS</h1>
      <Row className={classes.product}>
        {filteredData.map((item, index) => (
          <Col
            key={index}
            span={4}
            className={classes.col}
            onClick={() => changeProductHandler(item)}
          >
            <img src={item.img1} alt="" />
            <p className={classes.itemName}>{item.name}</p>
            <p className={classes.itemPrice}>{item.price} VNĐ</p>
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default RelatedProduct;
