import { Outlet } from "react-router-dom";
import Navbar from "./Navbar";
import Footer from "./Footer";
import PopupMess from "./ModalMess";

const Layout = ({ onChangeState }) => {
  return (
    <>
      <PopupMess />
      <Navbar onChangeState={onChangeState} />
      <Outlet />
      <Footer />
    </>
  );
};
export default Layout;
