// Import Module
import classes from "./Input.module.css";

const Input = ({
  inputValueChangeHandler,
  inputInputBlurHandler,
  enteredValue,
  valueInputValid,
  showErrorMessage,
  placeholder,
  errorMessage,
  type,
}) => {
  const valueInputClasses = valueInputValid
    ? classes.errorMessage
    : classes.errorMessageInvalid;

  return (
    <>
      <div className={classes.container}>
        <input
          className={classes.input}
          placeholder={placeholder}
          onChange={inputValueChangeHandler}
          onBlur={inputInputBlurHandler}
          value={enteredValue}
          type={type}
        />
        <span className={classes.focusBorder}></span>
      </div>
      {showErrorMessage && (
        <p className={valueInputClasses}>(*) {errorMessage}</p>
      )}
    </>
  );
};

export default Input;
