// Import module
import classes from "./Banner.module.css";
import { useNavigate } from "react-router-dom";

const Banner = () => {
  //Create + use Hook
  const navigate = useNavigate();

  // Create event handler
  const navigateHandler = () => {
    navigate("/shop");
  };

  return (
    <div className={classes.banner}>
      <div className={classes.infoBanner}>
        <p className={classes.title}>NEW INSPRIRATION 2020</p>
        <h1 className={classes.discountContent}>20% OFF ON NEW SEASON</h1>
        <button className={classes.btnTransfer} onClick={navigateHandler}>
          Browse collections
        </button>
      </div>
    </div>
  );
};

export default Banner;
