// Import Module
import classes from "./InfoDetail.module.css";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

// Import Component
import { CaretLeftOutlined, CaretRightOutlined } from "@ant-design/icons";
import { addItemActions } from "../../redux/storeRedux";

const InfoDetail = ({ productDetail, stateAccount }) => {
  console.log("render");
  // Get API from LocalStorage
  const currentUser = JSON.parse(localStorage.getItem("CURRENT_USER")) || null;
  const cartData = JSON.parse(localStorage.getItem("CART_ARRAY")) || [];

  // Create + Use Redux
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const quantity = useSelector((state) => state.add.quantityItems);

  // Create Event Handler
  const increaseQuantityItemHandler = () => {
    dispatch(addItemActions.increaseQuantity());
  };

  const decreaseQuantityItemHandler = () => {
    if (quantity === 0) {
      return false;
    } else {
      dispatch(addItemActions.decreaseQuantity());
    }
  };

  const addItemHandler = () => {
    const selectedProduct = {
      ...productDetail,
      quantity: quantity,
      total: Number(productDetail.price.split(".").join("")) * quantity,
    };
    if (!stateAccount.isLogin && !stateAccount.logout) {
      const result = window.confirm("Bạn cần đăng nhập để mua sản phẩm !");
    } else {
      dispatch(
        addItemActions.addProduct({ selectedProduct, cartData, currentUser })
      );
      dispatch(addItemActions.reset());
    }
  };

  return (
    <div className={classes.container}>
      <h1 className={classes.name}>{productDetail.name}</h1>
      <h3 className={classes.price}>{productDetail.price} VNĐ</h3>
      <p className={classes.description}>{productDetail.short_desc}</p>
      <p className={classes.category}>
        CATEGORY: <span>{productDetail.category} </span>
      </p>
      <div className={classes.form}>
        <b>QUANTITY</b>
        <div className={classes.amountItem}>
          <CaretLeftOutlined
            className={classes.icon}
            onClick={decreaseQuantityItemHandler}
          />
          <span>{quantity}</span>
          <CaretRightOutlined
            className={classes.icon}
            onClick={increaseQuantityItemHandler}
          />
        </div>
        <button
          className={classes.btnAdd}
          type="primary"
          onClick={addItemHandler}
        >
          Add to cart
        </button>
      </div>
    </div>
  );
};

export default InfoDetail;

