// Import Module
import { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useSelector } from "react-redux";

//Import Component
import { ProductsContext } from "../store-context/ProductsContext";
import { Row, Col } from "antd";
import Banner from "../components/DetailPage/Banner";
import ImgProducts from "../components/DetailPage/ImgProducts";
import InfoDetail from "../components/DetailPage/InfoDetail";
import Description from "../components/DetailPage/Description";
import RelatedProduct from "../components/DetailPage/RelatedProduct";

const DetailPage = ({ logout }) => {
  // Get State of User when login
  const isLogin = JSON.parse(localStorage.getItem("IS_LOGIN"));

  //Get 2 value of params
  const { productID } = useParams();
  const [id, category] = productID.split("_");

  // Create + use Hook
  const dataContext = useContext(ProductsContext);
  const [productDetail, setProductDetail] = useState(null);
  useEffect(() => {
    let result = dataContext.find((item) => item._id.$oid === id);

    result = {
      ...result,
      images: [result.img1, result.img2, result.img3, result.img4],
    };
    setProductDetail(result);
  }, [productID]);

  return (
    <>
      <Banner />
      {productDetail && (
        <div>
          <Row justify="space-evenly" style={{ marginBottom: 50 }}>
            <Col span={8}>
              <ImgProducts productDetail={productDetail} />
            </Col>
            <Col span={12}>
              <InfoDetail
                productDetail={productDetail}
                stateAccount={{ isLogin, logout }}
              />
            </Col>
          </Row>
          <Description productDetail={productDetail} />
          <RelatedProduct category={category} id={id} />
        </div>
      )}
    </>
  );
};

export default DetailPage;
