const UseSort = (products, type) => {
  let resultSortProducts = [];
  const modifiedProducts = products.map((item) => {
    return {
      ...item,
      price: Number(item.price.split(".").join("")),
    };
  });
  if (type === "increase") {
    resultSortProducts = modifiedProducts.sort((a, b) => a.price - b.price);
  } else if (type === "decrease") {
    resultSortProducts = modifiedProducts.sort((a, b) => b.price - a.price);
  } else {
    resultSortProducts = products;
  }
  resultSortProducts.forEach((item) => {
    item.price = item.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  });

  return {
    data: resultSortProducts,
  };
};

export default UseSort;
