// Import Module
import classes from "./Category.module.css";
import { useContext, useEffect, useRef, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { ProductsContext } from "../../store-context/ProductsContext";

//Import Component
import { Row, Col, Input, Select, Pagination } from "antd";
import UseSort from "../../hooks/use-sortData";

const Category = () => {
  // Create + User Hook
  const navigate = useNavigate();
  const { category } = useParams();
  const dataContext = useContext(ProductsContext);
  const [products, setProducts] = useState(dataContext);
  const [searchedProducts, setSearchedProducts] = useState(products);
  const [sortProducts, setSortProducts] = useState(products);
  const [enteredNameProduct, setEnteredNameProduct] = useState("");

  // console.log("render", products);
  const [showData, setShowData] = useState({
    filterData: true,
    searchData: false,
    sortData: false,
  });

  useEffect(() => {
    filteredDataByParam();
  }, [category]);

  // Create Function
  const filteredDataByParam = () => {
    setShowData({ filterData: true, searchData: false, sortData: false });
    if (category === "all" || !category) {
      setProducts(dataContext);
    } else {
      const modifiedData = dataContext.filter(
        (item) => item.category === category
      );
      setProducts(modifiedData);
    }
  };

  // Create Event Hander
  const blurProductHandler = () => {
    setEnteredNameProduct("");
  };

  const searchProductHandler = (event) => {
    if (event.target.value.trim() !== "") {
      // Searchdata by content of input
      const searchedData = products.filter((item) =>
        item.name.includes(event.target.value)
      );
      setEnteredNameProduct(event.target.value);
      setSearchedProducts(searchedData);
      setShowData({ filterData: false, searchData: true, sortData: false });
    } else {
      setProducts(products);
      setEnteredNameProduct(event.target.value);
      setShowData({ filterData: true, searchData: false, sortData: false });
    }
  };

  const sortProductHandle = (value) => {
    const { data: dataSort } = UseSort(products, value);
    setSortProducts(dataSort);
    setShowData({ filterData: false, searchData: false, sortData: true });
  };

  const showDetailProductHandler = (item) => {
    navigate(`/detail/${item._id.$oid}_${item.category}`);
  };

  return (
    <div className={classes.category}>
      <Row className={classes.row} justify="space-between">
        <Col span={10}>
          <Input
            className={classes.inputSearch}
            onChange={searchProductHandler}
            onBlur={blurProductHandler}
            placeholder="Enter Search Here"
            value={enteredNameProduct}
          />
        </Col>
        <Col span={10}>
          <Select
            defaultValue="Select Sorting"
            style={{ width: "100%" }}
            onChange={sortProductHandle}
            options={[
              { value: "default", label: "Default" },
              { value: "increase", label: "Prices gradually increase" },
              { value: "decrease", label: "Prices gradually decrease" },
            ]}
          />
        </Col>
      </Row>
      <Row className={classes.row}>
        {showData.filterData &&
          products.map((item) => (
            <Col
              key={item._id.$oid}
              span={5}
              className={classes.item}
              onClick={() => {
                showDetailProductHandler(item);
              }}
            >
              <img src={item.img1} alt="" />
              <p className={classes.itemName}>{item.name}</p>
              <p className={classes.itemPrice}>{item.price} VNĐ</p>
            </Col>
          ))}
        {showData.searchData &&
          searchedProducts.map((item) => (
            <Col
              key={item._id.$oid}
              span={5}
              className={classes.item}
              onClick={() => {
                showDetailProductHandler(item);
              }}
            >
              <img src={item.img1} alt="" />
              <p className={classes.itemName}>{item.name}</p>
              <p className={classes.itemPrice}>{item.price} VNĐ</p>
            </Col>
          ))}
        {showData.sortData &&
          sortProducts.map((item) => (
            <Col
              key={item._id.$oid}
              span={5}
              className={classes.item}
              onClick={() => {
                showDetailProductHandler(item);
              }}
            >
              <img src={item.img1} alt="" />
              <p className={classes.itemName}>{item.name}</p>
              <p className={classes.itemPrice}>{item.price} VNĐ</p>
            </Col>
          ))}
      </Row>
      <Pagination
        className={classes.pagination}
        defaultCurrent={1}
        total={50}
      />
    </div>
  );
};

export default Category;
