// Import Module
import classes from "./Footer.module.css";

// Import Component
import { Row, Col } from "antd";
import { Link } from "react-router-dom";

//Data Footer: DUMMY_LINK
const DUMMY_LINK = [
  {
    id: 1,
    title: "CUSTOMER SERVICES",
    links: [
      "Help & Contact Us",
      "Returns & Refunds",
      "Online Stores",
      "Terms & Condition",
    ],
  },
  {
    id: 2,
    title: "COMPANY",
    links: ["What we do", "Available Services", "Lastest Posts", "FAQs"],
  },
  {
    id: 3,
    title: "SOCIAL MEDIA",
    links: ["Twitter", "Instagram", "Facebook", "Pinterest"],
  },
];

const Footer = () => {
  return (
    <div className={classes.footer}>
      <Row className={classes.container} justify="space-around">
        {DUMMY_LINK.map((item, index) => (
          <Col
            className={classes.col}
            key={item.id}
            lg={8}
            md={8}
            sm={8}
            xs={24}
          >
            <p>{item.title}</p>
            <div className={classes.listLink}>
              {item.links.map((link, index) => (
                <Link key={index} to="#">
                  {link}
                </Link>
              ))}
            </div>
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default Footer;
