// Import Module
import classes from "./Navbar.module.css";
import { useDispatch, useSelector } from "react-redux";

// Import Component
import { Col, Row } from "antd";
import { NavLink } from "react-router-dom";
import {
  ShoppingCartOutlined,
  UserOutlined,
  LoginOutlined,
} from "@ant-design/icons";
import { loginActions } from "../redux/storeRedux";
import { useEffect, useState } from "react";

const Navbar = ({ onChangeState }) => {
  // Get API LocalStorage
  let isLogin = JSON.parse(localStorage.getItem("IS_LOGIN"));
  const currentUser = JSON.parse(localStorage.getItem("CURRENT_USER")) || null;
  const cartData = JSON.parse(localStorage.getItem("CART_ARRAY")) || [];

  // Create + Use Hook
  const [stateLogin, setStateLogin] = useState(isLogin);
  const [amountCart, setAmountCart] = useState(0);
  const amount = useSelector((state) => state.add.amountCarts);
  const { amountDeleteItems } = useSelector((state) => state.update);

  const dispatch = useDispatch();
  useEffect(() => {
    if (currentUser) {
      const existUser = cartData.find(
        (item) => item.user === currentUser.email
      );
      if (existUser) {
        setAmountCart(existUser.items.length);
      }
    } else {
      setAmountCart(0);
    }
  }, [amount, amountDeleteItems]);

  // Use redux
  const logOutHandler = () => {
    dispatch(loginActions.logout());
    setStateLogin(false);
    onChangeState(false);
    localStorage.removeItem("CURRENT_USER");
  };
  return (
    <div className={classes.navbar}>
      <Row className={classes.container} justify="center" align="middle ">
        <Col className={classes.colLeft} lg={8} md={6} sm={7} xs={10}>
          <ul>
            <li>
              <NavLink
                to="/"
                className={({ isActive }) =>
                  isActive ? classes.active : undefined
                }
              >
                Home
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/shop"
                className={({ isActive }) =>
                  isActive ? classes.active : undefined
                }
              >
                Shop
              </NavLink>
            </li>
          </ul>
        </Col>
        <Col className={classes.colCenter} lg={8} md={8} sm={8} xs={24}>
          <ul>
            <li>
              <NavLink to="/">BOUTIQUE</NavLink>
            </li>
          </ul>
        </Col>
        <Col className={classes.colRight} lg={8} md={8} sm={8} xs={12}>
          <ul>
            <li>
              <NavLink
                to="/cart"
                className={({ isActive }) =>
                  isActive ? classes.active : classes.cart
                }
              >
                <ShoppingCartOutlined
                  className={classes.icon}
                  style={{ fontSize: "20px" }}
                />
                Cart
                {stateLogin && (
                  <span className={classes.quantity}>{amountCart}</span>
                )}
              </NavLink>
            </li>
            <li>
              {stateLogin ? (
                <>
                  <NavLink
                    to="login"
                    className={({ isActive }) =>
                      isActive ? classes.active : classes.userLogin
                    }
                  >
                    <UserOutlined
                      className={classes.icon}
                      style={{ fontSize: "20px" }}
                    />
                    {currentUser.name}
                  </NavLink>
                  <span onClick={logOutHandler} className={classes.logout}>
                    <LoginOutlined
                      className={classes.icon}
                      style={{ fontSize: "20px" }}
                    />
                    Logout
                  </span>
                </>
              ) : (
                <NavLink
                  to="login"
                  className={({ isActive }) =>
                    isActive ? classes.active : undefined
                  }
                >
                  <UserOutlined
                    className={classes.icon}
                    style={{ fontSize: "20px" }}
                  />
                  Login
                </NavLink>
              )}
            </li>
          </ul>
        </Col>
      </Row>
    </div>
  );
};

export default Navbar;
