// Import module
import classes from "./Categories.module.css";
import { useNavigate } from "react-router-dom";

//import Component
import { Row, Col } from "antd";

//import image
import imgProduct1 from "../../imgs/product_1.png";
import imgProduct2 from "../../imgs/product_2.png";
import imgProduct3 from "../../imgs/product_3.png";
import imgProduct4 from "../../imgs/product_4.png";
import imgProduct5 from "../../imgs/product_5.png";

const Categories = () => {
  // Create a array get Images
  const listImageProduct = [
    { id: 1, img: imgProduct1 },
    { id: 2, img: imgProduct2 },
    { id: 3, img: imgProduct3 },
    { id: 4, img: imgProduct4 },
    { id: 5, img: imgProduct5 },
  ];

  // Create + Use Hook
  const navigate = useNavigate();

  const navigateHandler = () => {
    navigate("/shop");
  };

  return (
    <div className={classes.categories}>
      <p>CAREFULLY CREATED COLLECTIONS</p>
      <h2>BROWSE OUR CATEGORIES</h2>
      <Row className={classes.row}>
        {listImageProduct.map((item) => (
          <Col key={item.id} className={classes.col}>
            <img src={item.img} alt={`image-product${item.id}`} />
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default Categories;
