import { configureStore, createSlice } from "@reduxjs/toolkit";
// Create Initial State
const initialPopup = { isShow: false };
const initialModalMess = { openModal: false };
const initialStateLogin = { isLogin: false };
const initialQuantity = {
  quantityItems: 0,
  amountCarts: 0,
};
const initialProduct = { quantityItems: 0, amountDeleteItems: 0 };

// Create Slice
const updateSlice = createSlice({
  name: "update",
  initialState: initialProduct,
  reducers: {
    increaseProduct(state, action) {
      const { itemSelected, dataAPI, currentUser } = action.payload;
      // Tìm Product trong Cart của User
      const findProduct = currentUser.items.find(
        (item) => item._id.$oid === itemSelected._id.$oid
      );

      const updateFindProduct = {
        ...findProduct,
        quantity: findProduct.quantity + 1,
        total:
          findProduct.total + Number(findProduct.price.split(".").join("")),
      };
      currentUser.items[currentUser.items.indexOf(findProduct)] =
        updateFindProduct;
      localStorage.setItem("CART_ARRAY", JSON.stringify(dataAPI));
      return { ...state, quantityItems: state.quantityItems + 1 };
    },
    decreaseProduct(state, action) {
      const { itemSelected, dataAPI, currentUser } = action.payload;
      // Tìm Product trong Cart của User
      const findProduct = currentUser.items.find(
        (item) => item._id.$oid === itemSelected._id.$oid
      );
      // Kiểm tra nếu Quantity
      if (findProduct.quantity > 0) {
        const updateFindProduct = {
          ...findProduct,
          quantity: findProduct.quantity - 1,
          total:
            findProduct.total - Number(findProduct.price.split(".").join("")),
        };
        currentUser.items[currentUser.items.indexOf(findProduct)] =
          updateFindProduct;
        localStorage.setItem("CART_ARRAY", JSON.stringify(dataAPI));
        return { ...state, quantityItems: state.quantityItems - 1 };
      } else {
        return false;
      }
    },

    deleteProduct(state, action) {
      const { itemSelected, dataAPI, currentUser } = action.payload;
      // Tìm Product trong Cart của User
      const findProduct = currentUser.items.findIndex(
        (item) => item._id.$oid === itemSelected._id.$oid
      );
      currentUser.items.splice(findProduct, 1);
      localStorage.setItem("CART_ARRAY", JSON.stringify(dataAPI));
      return { ...state, amountDeleteItems: state.amountDeleteItems + 1 };
    },
  },
});

const popupSlice = createSlice({
  name: "popup",
  initialState: initialPopup,
  reducers: {
    showModal(state) {
      state.isShow = !state.isShow;
    },
  },
});
const modalMessSlice = createSlice({
  name: "popup",
  initialState: initialModalMess,
  reducers: {
    openModal(state) {
      state.openModal = !state.openModal;
    },
  },
});
const loginSlice = createSlice({
  name: "login",
  initialState: initialStateLogin,
  reducers: {
    login(state) {
      state.isLogin = true;
      localStorage.setItem("IS_LOGIN", state.isLogin);
    },
    logout(state) {
      state.isLogin = false;
      localStorage.setItem("IS_LOGIN", state.isLogin);
    },
  },
});

const addItemSlice = createSlice({
  name: "add",
  initialState: initialQuantity,
  reducers: {
    // Update by Inmutable NOT Mutable

    increaseQuantity(state) {
      return { ...state, quantityItems: state.quantityItems + 1 };
    },
    decreaseQuantity(state) {
      return { ...state, quantityItems: state.quantityItems - 1 };
    },
    reset(state) {
      return { ...state, quantityItems: 0 };
    },
    addProduct(state, action) {
      const { selectedProduct, cartData, currentUser } = action.payload;
      if (state.quantityItems === 0) {
        alert("Số lượng phải lớn hơn 0 mới thêm được vào giỏ hàng!");
        return false;
      } else {
        // Check mảng chưa có sản phẩm
        if (cartData.length === 0) {
          cartData.push({
            user: currentUser.email,
            items: [selectedProduct],
          });
          const existUser = cartData.find(
            (item) => (item.user = currentUser.email)
          );
          localStorage.setItem("CART_ARRAY", JSON.stringify(cartData));
          return { ...state, amountCarts: existUser.items.length };
        }
        // Check mảng có sản phẩm
        else {
          // Lấy user hiện tại
          const existUser = cartData.find(
            (item) => item.user === currentUser.email
          );
          if (existUser) {
            // Tìm sản phẩm trùng trong mảng và cập nhật
            const existProduct = existUser.items.find(
              (element) => element._id.$oid === selectedProduct._id.$oid
            );
            if (existProduct) {
              // Cập nhật quantity + total của sản phẩm
              const updatedExistProduct = {
                ...existProduct,
                quantity: existProduct.quantity + selectedProduct.quantity,
                total:
                  Number(selectedProduct.price.split(".").join("")) *
                  (existProduct.quantity + selectedProduct.quantity),
              };
              // Cập nhật updatedExistProduct vào mảng existUser.items
              existUser.items[existUser.items.indexOf(existProduct)] =
                updatedExistProduct;
            } else {
              existUser.items.push(selectedProduct);
            }
          } else {
            return false;
          }
          localStorage.setItem("CART_ARRAY", JSON.stringify(cartData));
          return { ...state, amountCarts: existUser.items.length };
        }
      }
    },
  },
});

const store = configureStore({
  reducer: {
    popup: popupSlice.reducer,
    login: loginSlice.reducer,
    add: addItemSlice.reducer,
    update: updateSlice.reducer,
    modalMess: modalMessSlice.reducer,
  },
});

// Create Actions Redux
export const popupActions = popupSlice.actions;
export const loginActions = loginSlice.actions;
export const addItemActions = addItemSlice.actions;
export const updateProductActions = updateSlice.actions;
export const modalMessActions = modalMessSlice.actions;

export default store;
